          this.modalShow = false;
      },
      openModal(id) {
          this.modalShow = true;
          this.openedItem = this.all.filter((item) => item.id === id)[0];
      },
      categorySelect(category) {
          if( category === 'all') {
              this.categoryToRender = 'all'
          }
          else if(category === 'clothes') {
              this.categoryToRender = 'clothes'
          }
          else if(category === 'accessories') {
